@font-face {
    font-family: "e-Ukraine";
    src: url(../fonts/e-Ukraine-Regular.otf) format('otf');
}

html, body, h1, h2, h3, h4, h5, p, a, button{
    font-family: 'e-Ukraine';
    font-weight: 100;
    letter-spacing: -.02rem;
    vertical-align:baseline;
    margin:0;
    padding:0;
    border:0;
    color:#fff;
}

body{
    height: 100%;
    width: 100%;
    background-color: black;
    z-index: -200;
    justify-content: center;
    align-items: center;
}

header{
    position: fixed;
    background-color: #000;
    display: flex;
    justify-content: center;
	top: 0;
	left: 0;
	width: 100%;
	text-align: center;
	z-index: 9999;
	transition: all 300ms ease-in-out;
}

nav{
    display: flex;
}

h1{
    font-size: clamp(28px,3.87vw,56px);
    font-weight: 900;
    line-height: 107%;
    margin-block-end: calc(clamp(28px,3.87vw,56px)/2);
}

h2{
    font-size: clamp(24px,2.62vw,38px);
    font-weight: 500;
    line-height: 105%;
    margin-block-end: calc(clamp(24px,2.62vw,38px)/2);
}

h3{
    font-size: clamp(20px,1.95vw,28px);
    font-weight: 400;
    line-height: 114%;
    margin-block-end: calc(clamp(20px,1.95vw,28px)/4);
}

h4{
    font-size: clamp(18px,1.68vw,24px);
    font-weight: 300;
    line-height: 117%;
    margin-block-end: calc(clamp(18px,1.68vw,24px)/5);    
}

h5{
    font-size: clamp(16px,1.37vw,20px);
    line-height: 120%;
    margin-block-end: clamp(16px,1.37vw,20px);
}

p{
    font-size: clamp(14px,1.2vw,16px);
    max-inline-size: 66ch;
    font-weight: 200;
    line-height: 150%;
    letter-spacing: 0.1rem;
    margin-block-end: calc(clamp(14px,1.2vw,16px)/0.5);
}

img{
    box-sizing: border-box;
    width: auto;
    height: 100%;
}

button {
    background: #343434;
    outline:0;
    color:#fff;
    cursor:pointer;
    text-decoration:none;
    border-radius:8px;
    padding-inline: 16px;
    padding-block: 12px;
}


::-webkit-scrollbar{
    background-color:rgba(0,0,0,0);
    width:16px
}
::-webkit-scrollbar-track{
    background-color:rgba(0,0,0,0)
}
::-webkit-scrollbar-track:hover{
    background-color:rgba(0,0,0,0)
}
::-webkit-scrollbar-thumb{
    background-color:#343434;
    border-radius:8px;
    border:6px solid #000
}
::-webkit-scrollbar-thumb:hover{
    background-color:#fff;
    border:6px solid #000
}
::-webkit-scrollbar-button{
    display:none
}